import {useNavigate} from "react-router-dom";

import {CREATION_LINK, HISTORY_LINK} from "../../../shared/config/config";

import {IncidentItemType} from "../../../shared/types/incident-Item.type";

import "./PopupIncidentItem.scss";

interface Props {
    onClose: () => void;
    incidentItem: IncidentItemType;
}

function PopupIncidentItem({onClose, incidentItem}: Props) {
    const navigate = useNavigate();

    const level: string = incidentItem.level === "basement" ? "Подвал" : incidentItem.level === "floor" ? "1 этаж" : "Чердак";

    return (
        <div className="incident">
            <div className="incident__aside"></div>
            <div className="incident__content">
                <div className="incident__header">
                    <div className="incident__title">Инцидент</div>
                    <button className="incident__close-button" onClick={onClose}></button>
                </div>
                <div className="incident__body">
                    <div className="incident__info">
                        <div className="incident__info-title-items">
                            <p className="incident__info-title">Здание</p>
                            <p className="incident__info-title">Точка установки</p>
                            <p className="incident__info-title">Дата и время</p>
                            <p className="incident__info-title">Датчик</p>
                            <p className="incident__info-title">Статус</p>
                        </div>
                        <div className="incident__info-text-items">
                            <p className="incident__info-text">{incidentItem.address}</p>
                            <p className="incident__info-text">{level}</p>
                            <p className="incident__info-text">{incidentItem.date}</p>
                            <p className="incident__info-text">{incidentItem.detector}</p>
                            <p className="incident__info-text">{incidentItem.status}</p>
                        </div>
                    </div>
                    <div className="incident__actions">
                        <button className="btn btn-light incident__button" onClick={() => navigate(HISTORY_LINK)}>Подробнее</button>
                        <button className="btn btn-primary incident__button" onClick={() => navigate(CREATION_LINK)}>Создать заявку</button>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default PopupIncidentItem;