import {Dispatch, SetStateAction, useState} from "react";
import {useNavigate} from "react-router-dom";

import TableMain from "../../components/TableMain/TableMain";
import PopupIncidentItem from "../../components/PopupIncidentItem/PopupIncidentItem";

import {MKD_LINK} from "../../../shared/config/config";
import {defaultIncidentItem} from "../../../shared/config/default-incident-item";

import mapPin from "../../../assets/images/icons/map/map-pin.svg"

import {MkdItemType} from "../../../shared/types/mkd-Item.type";
import {IncidentItemType} from "../../../shared/types/incident-Item.type";

import './Main.scss';

interface Props {
    mkdItemsList: MkdItemType[];
    setMkdItemsList: Dispatch<SetStateAction<MkdItemType[]>>;
    setMkdItemCurrent: Dispatch<SetStateAction<MkdItemType | undefined>>;
}

function Main({mkdItemsList, setMkdItemsList, setMkdItemCurrent}: Props) {
    const navigate = useNavigate();

    const [isIncidentPopupOpen, setIncidentPopupOpen] = useState<boolean>(false);
    const [incidentItem, setIncidentItem] = useState<IncidentItemType>(defaultIncidentItem);

    function handleAddressClick(event: any) {
        const currentId = event.currentTarget.id;
        const currentItem: MkdItemType | undefined = mkdItemsList.find(item => item.id === currentId);
        currentItem && setMkdItemCurrent(currentItem);
        navigate(MKD_LINK);
    }

    function handleIncident(event: any) {
        const currentId = event.currentTarget.id;
        const currentItem: MkdItemType | undefined = mkdItemsList.find(item => item.id === currentId);

        const mkdElements: HTMLCollectionOf<Element> = document.getElementsByClassName("mkd");
        const currentMkdElement: Element | undefined = Array.from(mkdElements).find(item => item.id.includes(currentId));
        currentMkdElement && currentMkdElement.classList.add("mkd_incident");

        const accordionMkdElements: HTMLCollectionOf<Element> = document.getElementsByClassName("item-in");
        const currentAccordionMkdElement: Element | undefined = Array.from(accordionMkdElements).find(item => item.id.includes(currentId));
        currentAccordionMkdElement && currentAccordionMkdElement.classList.add("item-in_incident");

        const tableMkdElements: HTMLCollectionOf<Element> = document.getElementsByClassName("table-main__tr");
        const currentTableMkdElement: Element | undefined = Array.from(tableMkdElements).find(item => item.id.includes(currentId));
        currentTableMkdElement && currentTableMkdElement.classList.add("table-main__tr_incident");

        const date: Date = new Date();
        const currentDate: string = date.getDay() + "." + (date.getMonth() + 1) + "." + date.getFullYear() + ", " + date.getHours() + ":" + date.getMinutes();

        const filteredMkdItems: MkdItemType[] = mkdItemsList.filter(item => item.id !== currentId);

        if (currentItem) {
            currentItem.status = "Incident";
            setMkdItemsList([
                ...filteredMkdItems,
                currentItem
            ]);

            setIncidentItem({
                id: 0,
                address: currentItem.address,
                level: "floor",
                date: currentDate,
                detector: "CO2",
                status: "Сработал"
            });
        }

        setIncidentPopupOpen(true);
    }

    function handleClose() {
        setIncidentPopupOpen(false);
    }

    return (
        <div className="main" id="main">
            <div className="mkd-items">
                {mkdItemsList.map((item: MkdItemType, index: number) => (
                    <div key={index}
                         className={`mkd mkd_${item.id}`}
                         id={`${item.id}`} style={{left: item.left, top: item.top}}
                         title="Двойной клик для просмотра"
                         onClick={handleIncident}
                         onDoubleClick={handleAddressClick}>
                        <img className="mkd__pin-image" src={mapPin} alt="Иконка указателя местоположения"/>
                        <p className="mkd__address">{item.address}</p>
                    </div>
                ))}
            </div>

            <div className="mkd-table">
                <TableMain/>
            </div>

            {isIncidentPopupOpen && <PopupIncidentItem onClose={handleClose} incidentItem={incidentItem}/>}
        </div>
    );
}

export default Main;